/*
  Sizes are all expressed in terms of 14px/em (default em)
  This ensures these "ratios" remain constant despite changes in EM
*/

$miniSize        : (11 / 14);
$tinySize        : (12 / 14);
$smallSize       : (13 / 14);
$mediumSize      : (14 / 14);
$largeSize       : (16 / 14);
$bigSize         : (18 / 14);
$hugeSize        : (20 / 14);
$massiveSize     : (24 / 14);

